.App {
  text-align: center;
}

.App-header {
  background-color: #FFF;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
}

h2 {
  text-align: left;
  padding: 1em 6em 0;
}

ul.App-list {
  padding: 0 8em;
  color: black;
  text-align: left;
}

li.App-list-item {
  list-style: none;
  margin: 1em;
}

.App-link {
  color: #61dafb;
}
